@import '~assets/styles/h3Imports';

.header {
  @media (max-width: $max-phone-size) {
    margin: em(24) 0 em(16);
  }

  .title {
    font-size: em(27);
  }
}

.description {
  line-height: line-height(24, 16);
  margin-bottom: em(16);
  margin-top: 0;
}

.provider {
  .provider-name {
    font-weight: 700;
    margin: 0;
  }

  .provider-info {
    align-items: center;
    display: flex;
    gap: em(16);

    .provider-description {
      @include paragraph-small;

      color: var(--gray-3);
    }
  }

  .provider-contacts {
    align-items: center;
    display: flex;
    justify-content: space-between;

    &[data-modal-provider-devider='true'] {
      border-top: 1px solid var(--modal-button-border);
      margin-top: var(--spacing-s);
      padding-top: var(--spacing-s);

      @media (max-width: $max-phone-size) {
        margin-top: var(--spacing-xs);
        padding-top: var(--spacing-xs);
      }
    }

    .provider-phone {
      align-items: center;
      color: var(--text-color);
      display: flex;
      font-weight: 400;
      gap: em(8);
      left: em(4);
      margin: em(16) 0;
      position: relative;
      text-decoration: none;

      @media (max-width: $max-phone-size) {
        margin-bottom: 0;
      }
    }
  }
}

.working-hours {
  list-style-type: none;
  margin-bottom: em(24);
  padding-left: 0;
  width: em(320);

  &-title {
    margin: 0;
  }

  &-item {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &-day,
  &-time-period {
    color: var(--gray-3);
    font-size: em(14);
    font-weight: 600;
    line-height: em(20);
  }

  &-period {
    display: flex;
    justify-content: space-between;
    width: 45%;
  }

  &-today {
    .working-hours-day {
      color: var(--black);
      font-weight: 700;
    }

    .working-hours-time {
      color: var(--black);
      font-weight: 600;
    }
  }
}

.footer-root {
  &[data-modal-footer-devider='true'] {
    @media (max-width: $max-phone-size) {
      padding-top: var(--spacing-xs);
    }
  }

  .buttons {
    @media (max-width: $max-tablet-size) {
      width: 100%;
    }
  }

  .button {
    &[data-button-width='true'][data-size] {
      width: 100%;
    }
  }
}
